header .membership{
  width: 30%; 
  @include responsive($breakpoint-small){
    width: 80%;    
  }
}
.membership{
  @include component_size;
  text-align: center;  
  p{@include responsive($breakpoint-small){
   width: 100%;
   font-size: $font-size-small;
  }
  }
}
.email-box{
  margin: 20px auto;  
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  @include responsive($breakpoint-small){
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  label{
    position: absolute;
    color: #aaa;
    left: 1rem;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s;
    @include responsive($breakpoint-small){
      top: 25%;
      font-size: $font-size-small;
    }
  }
  &.focused{
    label{
      top: 15px;
      font-size: 10px;      
    }
  }
  input{    
    flex: 1;    
    font-size: $font-size-medium;    
    padding: 0.5rem 1rem;
    padding-top: 20px;
    margin-right: 10px;
    transition: 0.3s;
    @include responsive($breakpoint-small){
      width: 100%;
      font-size: $font-size-small;
      padding-top: 10px;
    }
  }
  button{
    font-size: $font-size-medium;      
    @include button-style;
    @include responsive($breakpoint-small){
      width: 50%;          

    }
  }
}

