header{
  height: 80vh;
  background-image: linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.7)),url('../../public/images/netflixbackground.jpg'); 
  // background-image: url('../../public/images/bg.jpg');
  background-position: top center;
}

nav{
  width: 80%;
  margin: 0 auto;
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
  @include responsive($breakpoint-medium){
    width: 95%;
  }
  h1{
    color: $primary-color;
    font-size: $font-size-large;
  }
  div{
    select{
      padding: 0.4rem 1rem;
      margin-right: 10px;
      background-color: transparent;
      color: $text-color;
      option{
        background-color: #fff;
        color: #000;
      }
    }
    button{
      @include button-style;
    }
  }
}

.header-content{
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  h2{
    font-size: 60px;
    margin-bottom: 20px;
    @include responsive($breakpoint-medium){
      font-size: 40px;
    }   
  }
  h4{
    font-size: $font-size-medium;
    margin-bottom: 20px;
    @include responsive($breakpoint-small){
      font-size: 15px;
    }
  }
  p{
    font-size: $font-size-medium;
  }
}

